import React from 'react';
import style from './Contact.module.css'
import styleContainer from "../common/styles/Conteiner.module.css";
import Title from "../components/title/Title";
import Fade from 'react-reveal/Fade';


function Contact() {
    return (
        <div className={style.contactBlock}>
            <div className={`${styleContainer.container} ${style.contactContainer}`}>
                <Fade bottom big>
                <Title text={'Contact'}/>
                <div className={style.formBlock}>
                    <form className={style.block}>
                        <div className={style.ajaxBlock}>
                            <div className={style.formGroup}>
                            <input type="text" name="name" className={style.emailName} placeholder="Name"/>
                            </div>
                                <div className={style.formGroup}>
                            <input type="email" name="email" className={style.emailName} placeholder="Email"/>
                                </div>
                            <div className={style.formGroup}>
                            <textarea name="text" className={style.message} placeholder="Message"></textarea>
                            </div>
                            <button type="submit" className={style.send}>SEND MESSAGE</button>
                        </div>
                    </form>
                </div>
                    </Fade>
            </div>
        </div>
    );
}

export default Contact;
