import React from 'react';
import style from './MyWorks.module.css'
import styleContainer from "../common/styles/Conteiner.module.css";
import Work from "./work/Work";
import Title from "../components/title/Title";
import socialImage from "./work/img/block2.jpg"
import todoImage from "./work/img/block1.jpg"
import Flip from 'react-reveal/Flip';

function MyWorks() {
    const social = {
        backgroundImage: `url(${socialImage})`
    }

    const todolist = {
        backgroundImage: `url(${todoImage})`
    }
    return (
        <div className={style.myWorksBlock}>

            <div className={`${styleContainer.container} ${style.myWorksContainer}`}>
                <Flip>
                    <Title text={'Projects'}/>
                    <div className={style.myWork}>
                            <Work style={social} title={'Social Network'} description={'надо кнопку отстилизовать'}/>
                            <Work style={todolist} title={'Todolist'} description={'такая же песня'}/>
                    </div>
                </Flip>
            </div>
        </div>
    );
}

export default MyWorks;
