import React from 'react';
import style from './Skills.module.css'
import styleContainer from './../common/styles/Conteiner.module.css'
import Skill from "./skill/Skill";
import Title from "../components/title/Title";
import html from "../common/img/html.svg"
import css from "../common/img/css3.svg"
import js from "../common/img/javascript.svg"
import ts from "../common/img/typescript.svg"
import react from "../common/img/react.svg"
import redux from "../common/img/redux.svg"
import git from "../common/img/github.svg"
import Flip from 'react-reveal/Flip';

function Skills() {
    const html5 = {
        backgroundImage: `url(${html})`
    }
    const css3 = {
        backgroundImage: `url(${css})`
    }
    const javascript = {
        backgroundImage: `url(${js})`
    }
    const typescript = {
        backgroundImage: `url(${ts})`
    }
    const reactN = {
        backgroundImage: `url(${react})`
    }
    const reduxR = {
        backgroundImage: `url(${redux})`
    }
    const github = {
        backgroundImage: `url(${git})`
    }
    return (
        <div className={style.skillsBlock}>
            <div className={`${styleContainer.container} ${style.skillsContainer}`}>
                <Flip>
                <Title text={'Skills'}/>
                <div className={style.skills}>
                    {/*<div className={style.myWork}>*/}
                    <Skill style={html5} title={'HTML'}
                           description={'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod'}/>
                    <Skill style={css3} title={'CSS'}
                           description={'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod'}/>
                    <Skill style={javascript} title={'JS'} description={'ffffffffffffffff' +
                    'fffLorem ipsum dolor sit amet, consectetur adipisicing elit, sed do ' +
                    'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod ' +
                    'tempor incididunt ut labore et dolore magna aliqua Ut enim...eiusmod tempor' +
                    ' incididunt ut labore et dolore magna aliqua Ut enim...'}/>
                    <Skill style={typescript} title={'TS'}
                           description={'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod'}/>
                    <Skill style={reactN} title={'REACT'}
                           description={'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod'}/>
                    <Skill style={reduxR} title={'REDUX'}
                           description={'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod'}/>
                    <Skill style={github} title={'GIT'}
                           description={'Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod'}/>
                </div>
                    </Flip>
            </div>
        </div>
    );
}

export default Skills;
